import React, { useState } from 'react';
import * as API from './api';
import './App.css';

function App() {
  const [url, setUrl] = useState('');
  const [statusDL, setStatusDL] = useState('');
  const [fileName, setFileName] = useState('')

  const [tagArtist, setTagArtist] = useState('');
  const [tagTitle, setTagTitle] = useState('');
  const [tagAlbum, setTagAlbum] = useState('');
  const [urlImage, setUrlImage] = useState('');

  async function startDL(url) {
    let dataDL = await API.convertMp3(url, setStatusDL);
    setStatusDL('baixou o ', dataDL.data.audiofilename);
    setFileName(dataDL.data.audiofilename)

  }

  async function setTagsClick() {
    let data = {
      file: fileName,
      tags: {
        title: tagTitle,
        artist: tagArtist,
        album: tagAlbum,
        trackNumber: 1,
        image: urlImage
      }
    }

    API.setTags(data)
  }

  function saveOnMyPc() {
    API.getFileToPC(`${tagTitle} - ${tagArtist}.mp3`)
  }

  function delFromServer() {
    window.open('http://localhost:3333/delfromserver')
  }

  return (
    <div className="App">
      <div className="form-dl">
        <input
          className='input-dl'
          placeholder="YouTube Link"
          type="text"
          value={url}
          onChange={e => setUrl(e.target.value)} />

        <button className="btn-dl" onClick={() => startDL(url)}>Baixar</button>

        <h3 className="status-dl">{statusDL}</h3>
      </div>

      <div className="form-tags">
        <div>
          <input type="text"
            className='input-tags left-top'
            value={tagArtist}
            placeholder="Artista"
            onChange={e => setTagArtist(e.target.value)} />
          <input type="text"
            className='input-tags left-bottom'
            placeholder="Titulo"
            value={tagTitle}
            onChange={e => setTagTitle(e.target.value)} />
        </div>
        <div>
          <input type="text"
            className='input-tags right-top'
            placeholder="Album"
            value={tagAlbum}
            onChange={e => setTagAlbum(e.target.value)} />
          <input type="text"
            className='input-tags right-bottom'
            placeholder="URL da Imagem"
            value={urlImage}
            onChange={e => setUrlImage(e.target.value)} />
        </div>

        <button
          className="btn-tag"
          onClick={() => setTagsClick()}>Setar</button>

        <button
          className="btn-tag"
          onClick={() => saveOnMyPc()}>Salvar</button>

        <button
          className="btn-tag"
          onClick={() => delFromServer()}>Excluir</button>


      </div>
    </div>
  );
}

export default App;


