import axios from 'axios';

const SERVER_URL = 'http://localhost:3333';



async function convertMp3(url, setStatusDL) {

    let id = url.substr(32);
    setStatusDL('Baixando o ' + id)

    const yturl = {
        ytid: id
    }

    try {
        return await axios.post(SERVER_URL + '/sendid', yturl)
    } catch (e) {
        console.error(e)
    }
}

async function setTags(data) {

    try {
        return await axios.post(SERVER_URL + '/sendinfoaudio', data)
    } catch (e) {
        console.error(e)
    }
}

function getFileToPC(filename) {

    try {
        window.open(SERVER_URL + '/dlmp3/' + filename);
    } catch (e) {
        console.error(e)
    }
}

export { convertMp3, setTags, getFileToPC }